@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --main-color: #f20d0d;
}

.home-nav-section {
  display: flex;
  justify-content: space-between;
  padding: 0 25px;
  align-items: center;
  background: transparent;
  position: fixed;
  height: 120px;
  top: 0;
  width: 100%;
}
.home-nav-scroll {
  background: #000;
  display: flex;
  justify-content: space-between;
  padding: 0 25px;
  align-items: center;
  position: fixed;
  height: 120px;
  top: 0;
  width: 100%;
}
.home-nav-logo > h1 {
  color: var(--main-color);
  font-family: "Poppins";
  font-weight: 900;
  font-size: 48px;
  letter-spacing: 1.2px;
}
.home-nav-buttons {
  display: flex;
  align-items: center;
}
.home-nav-buttons > .MuiSvgIcon-root {
  color: var(--main-color);
  font-size: 18px;
  margin-right: 10px;
}
.home-nav-buttons > select {
  border: 1px solid lightgrey;
  cursor: pointer;
  outline: none;
  border-radius: 6px;
  margin-right: 10px;
  padding: 10px;
  color: var(--main-color);
  background-color: transparent;
}
.home-nav-buttons > button {
  cursor: pointer;
  background-color: var(--main-color);
  outline: none;
  border: 1px solid transparent;
  padding: 10px;
  border-radius: 8px;
  font-family: "Lato";
  font-weight: 700;
  color: #fff;
  font-size: 16px;
}
.home-nav-buttons > button:hover {
  background-color: transparent;
}

.hero-banner-section {
  padding-top: 120px;
  height: 100vh;
}
.hero-banner-section-info {
  display: flex;
  justify-content: space-around;
}
.hero-banner-info-left {
  padding-top: 185px;
  padding-left: 55px;
  max-width: 465px;
  color: #fff;
  font-size: 20px;
}
.hero-banner-info-left > button {
  margin-top: 25px;
  cursor: pointer;
  background-color: var(--main-color);
  outline: none;
  border: 1px solid transparent;
  padding: 10px;
  border-radius: 8px;
  font-family: "Lato";
  font-weight: 700;
  color: #fff;
  font-size: 16px;
}
.hero-banner-info-right {
  margin-top: 300px;
  height: 225px;
  background-color: rgba(0, 0, 0, 0.75);
  max-width: 450px;
  width: 425px;
  border-radius: 8px;
  box-shadow: 0px 5px 6px 6px rgba(255, 255, 255, 0.65);
}
.hero-banner-info-right > h4 {
  color: #fff;
  max-width: 275px;
  font-size: 26px;
  font-family: "Pacifico", "sans-serif";
  font-weight: 900;
  padding-top: 25px;
  padding-left: 25px;
}

.login {
  background-color: rgba(0, 0, 0, 0.95);
  height: 100vh;
}
.login-section {
  display: grid;
  place-items: center;
  height: 100vh;
}
.login-section > form {
  display: flex;
  flex-direction: column;
  padding: 85px;
  background-color: rgba(0, 0, 0, 0.55);
  border-radius: 10px;
  border: 1px solid var(--main-color);
}
.login-section > form > input {
  outline: none;
  border: 0.5px solid var(--main-color);
  padding: 16px 45px;
  border-radius: 4px;
  margin: 6px 0;
}
.login-btn {
  margin-top: 15px;
  cursor: pointer;
  background-color: var(--main-color);
  outline: none;
  border: 1px solid transparent;
  padding: 10px;
  border-radius: 6px;
  font-family: "Lato";
  font-weight: 700;
  color: #fff;
  font-size: 20px;
}
.register-btn {
  color: var(--main-color);
  padding-top: 6px;
  text-align: center;
  cursor: pointer;
  font-family: "Lato";
  font-weight: 400;
}

.movie-banner {
  margin-top: 120px;
  object-fit: contain;
  height: 100vh;
}
.movie-banner-info {
  padding-top: 155px;
  max-width: 625px;
  width: 600px;
  padding-left: 55px;
}
.movie-banner-title {
  padding: 25px 0;
}
.movie-banner-title > h1 {
  font-family: "Poppins";
  font-weight: 900;
  font-size: 52px;
  color: var(--main-color);
  letter-spacing: 1.2px;
}
.movie-banner-title > h4 {
  font-family: "Poppins";
  font-size: 32px;
  color: var(--main-color);
  padding-top: 15px;
}
.movie-banner-title > p {
  font-size: 32px;
  color: var(--main-color);
  font-family: "Lato";
  padding-top: 25px;
  font-weight: 700;
}
.movie-banner-btns {
  padding-left: 20px;
}
.movie-banner-btns > button {
  cursor: pointer;
  background-color: var(--main-color);
  outline: none;
  border: 1px solid transparent;
  padding: 15px;
  border-radius: 8px;
  font-family: "Lato";
  font-weight: 700;
  color: #fff;
  font-size: 16px;
  margin-left: 15px;
  margin-top: 10px;
}

.movie-row-section {
  background-color: #000;
  padding-bottom: 25px;
}
.movie-row-section > h1 {
  color: #fff;
  padding-top: 45px;
  font-family: "Pacifico";
  font-size: 38px;
  padding-left: 15px;
  letter-spacing: 1.5px;
}
.movie-row {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 90px 40px;
}
.movie-row::-webkit-scrollbar {
  display: none;
}
.movie-card {
  height: 300px;
  object-fit: contain;
  cursor: pointer;
  margin-left: 15px;
}
.movie-img {
  width: 250px;
  height: 100%;
}
.movieTitle {
  color: #fff;
  text-align: center;
  font-family: "Lato";
  font-weight: 400;
  font-size: 16px;
}
.movieCardSize {
  height: 400px;
  width: 350px;
  object-fit: contain;
}
.movieImgSize {
  width: 225px;
  height: 400px;
}
.movieVote {
  color: #fff;
  text-align: center;
}
.movieDate {
  color: #fff;
  display: flex;
  justify-content: center;
}
.movieDateSpan {
  margin-left: 8px;
  color: var(--main-color);
}
.movieAverage {
  color: #fff;
  display: flex;
  justify-content: center;
  color: var(--main-color);
}

